import React from 'react'
import Layout from "../../components/layout"
import { Link } from 'gatsby'
import SEO from "../../components/seo"
import { Row, Col, Container } from "react-bootstrap"
import styles from './index.css';

const CalculatorItem = ({children, description, ...props}) => {

  return (
    <Col xs={12} sm={6} md={6} lg={4}>
      <Col className="calculator-item" xs={12}>
        <Link to={props.to} className={styles.fw}>
          <Row>
            <Col xs={12}><h3>{children}</h3><hr /></Col>
            {description && <Col xs={12}><p>{description}</p></Col>}
          </Row>
        </Link>
      </Col>
    </Col>
  );
};

const Calculator = ({location}) => {
   return (
      <Layout location={location} title={"Calculators"} withFooter={false}>
        <SEO
          title={"Calculators."}
          description={"List of all calculators I have created over time."}
        />
        <Container fluid >
          <Row>
            <CalculatorItem
              to={"/calculators/money_prediction"}
              description="Do you know how much money will you have in the future? Let's look on that and compute that."
            >
              Money prediction
            </CalculatorItem>
          </Row>
        </Container>
      </Layout>
   );
}

export default Calculator
